@charset "UTF-8";
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
:root {
  --accent-font: "Oswald", "Impact", sans-serif;
  --main-font: "Arial", "Helvetica", sans-serif;
}

*,
::before,
::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  object-fit: cover;
}

:root {
  --text-color-accent: #000000;
  --text-color-main: #444444;
  --text-color-dark: #222222;
  --text-color-darken: #f2f2f2;
  --text-color-contrast: #ffffff;
  --bg-color-main: #ffffff;
  --bg-color-darken: #f2f2f2;
  --bg-color-dark: #eaeaea;
  --bg-color-darkest: #dcdcdc;
  --bg-color-accent: #68b738;
  --border-color-light: #e6e6e6;
  --border-color-lighten: #e7e7e7;
  --border-color-dark: #d9d9d9;
  --border-color-darken: #cdcdcd;
}

@custom-media --narrow-vp (width <= 767.9px);
@custom-media --not-narrow-vp (width >= 768px);
@custom-media --middle-vp (768px <= width < 1279.9px);
@custom-media --wide-vp (width >= 1280px);
@custom-media --not-wide-vp (width <= 1279.9px);
.page {
  font-family: var(--main-font);
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: var(--text-color-main);
  word-wrap: break-word;
  overflow-wrap: break-word;
}
@media (--not-narrow-vp) {
  .page {
    font-size: 16px;
    line-height: 24px;
  }
}

.page__body {
  --scrollbar: calc(100vw - 100%);
  margin: 0;
}

.page__centerer {
  --content-indent: calc(20px + (10vw - 32px) / 4.48);
  margin-inline: var(--content-indent) calc(var(--content-indent) - var(--scrollbar));
}
@media (--narrow-vp) {
  .page__centerer {
    --content-indent: calc(20px + (10vw - 32px) / 4.48);
  }
}
@media (--middle-vp) {
  .page__centerer {
    --content-indent: 30px;
  }
}
@media (--wide-vp) {
  .page__centerer {
    margin: 0 auto;
    width: 1220px;
  }
}

.page__title {
  margin-bottom: 40px;
}
@media (--not-narrow-vp) {
  .page__title {
    margin-bottom: 0;
  }
}
@media (--not-narrow-vp) {
  .page__title--form {
    margin-bottom: 66px;
  }
}

.page__banner {
  margin-bottom: 20px;
}
@media (--not-narrow-vp) {
  .page__banner {
    margin-bottom: 0;
  }
}
@media (--wide-vp) {
  .page__banner {
    margin-bottom: 82px;
  }
}

.page__modules {
  margin-bottom: 20px;
}
@media (--not-narrow-vp) {
  .page__modules {
    margin-bottom: 45px;
  }
}
@media (--wide-vp) {
  .page__modules {
    margin-bottom: 82px;
  }
}

.page__catalog {
  margin-bottom: 25px;
}
@media (--not-narrow-vp) {
  .page__catalog {
    margin-bottom: 68px;
  }
}
@media (--wide-vp) {
  .page__catalog {
    margin-bottom: 76px;
  }
}

.page__additional {
  margin-bottom: 35px;
}
@media (--not-narrow-vp) {
  .page__additional {
    margin-bottom: 76px;
  }
}
@media (--wide-vp) {
  .page__additional {
    margin-bottom: 86px;
  }
}

.page__brief {
  margin-bottom: 32px;
}
@media (--not-narrow-vp) {
  .page__brief {
    margin-bottom: 72px;
  }
}
@media (--wide-vp) {
  .page__brief {
    margin-bottom: 82px;
  }
}

.page__hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: block;
  width: fit-content;
  padding: 10px 20px;
  border: none;
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: var(--text-color-contrast);
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  background-color: var(--bg-color-accent);
  cursor: pointer;
}
.button--size_wide {
  width: 100%;
}
.button--color_gray {
  color: var(--text-color-main);
  background-color: var(--bg-color-darken);
}
@media (--wide-vp) {
  .button--size_big {
    height: 54px;
    padding: 17px 20px;
    font-size: 20px;
  }
}

.page-title {
  padding-top: 27px;
  font-family: var(--accent-font);
  color: var(--text-color-accent);
}
@media (--not-narrow-vp) {
  .page-title {
    padding-top: 0;
  }
}

.page-title__header {
  margin: 0;
  font-family: inherit;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}
@media (--not-narrow-vp) {
  .page-title__header {
    font-size: 60px;
    line-height: 60px;
  }
}

.page-title__text {
  margin: 0;
  font-family: inherit;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
}
@media (--not-narrow-vp) {
  .page-title__text {
    max-width: 330px;
    font-size: 20px;
    line-height: 30px;
  }
}

.page-title__header + .page-title__text {
  margin-top: 37px;
}
@media (--not-narrow-vp) {
  .page-title__header + .page-title__text {
    margin-top: 55px;
  }
}

.section-title {
  display: flex;
  align-items: center;
  width: 100%;
  margin: 0;
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: var(--text-color-accent);
}
@media (--not-narrow-vp) {
  .section-title {
    font-size: 36px;
    line-height: 36px;
  }
}
.section-title--decoration::after {
  --title-decoration-color: var(--bg-color-accent);
  content: "";
  flex-grow: 1;
  display: block;
  height: 2px;
  margin-left: 22px;
  background-color: var(--title-decoration-color);
  box-shadow: 30px 0 0 0 var(--title-decoration-color);
}
@media (--wide-vp) {
  .section-title--decoration::after {
    box-shadow: 30vw 0 0 0 var(--title-decoration-color);
  }
}

@media (--not-narrow-vp) {
  .main-header {
    padding-top: 24px;
    padding-bottom: 42px;
  }
}
@media (--wide-vp) {
  .main-header {
    padding-bottom: 64px;
  }
}
@media (--wide-vp) {
  .main-header {
    padding-top: 53px;
    padding-bottom: 29px;
  }
}

.main-header__container {
  position: relative;
}
@media (--not-narrow-vp) {
  .main-header__container {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
}

.main-header__logo {
  flex-shrink: 0;
}

@media (--not-narrow-vp) {
  .main-header__nav {
    flex-grow: 1;
    padding-top: 18px;
  }
}

.main-nav {
  border-bottom: 1px solid var(--border-color);
  font-family: var(--accent-font);
}
@media (--not-narrow-vp) {
  .main-nav {
    padding: 0;
    background: none;
  }
}
.main-nav--no-js .main-nav__toggler {
  display: none;
}
.main-nav--no-js .main-nav__list {
  position: static;
}

.main-nav__toggler {
  position: absolute;
  top: 22px;
  right: 0;
  width: 24px;
  height: 18px;
  border-radius: 0;
  border: none;
  background-color: var(--bg-color-accent);
  cursor: pointer;
}
@media (--not-narrow-vp) {
  .main-nav__toggler {
    display: none;
  }
}
.main-nav__toggler--closed {
  background-color: #ff0000;
}

.main-nav__list {
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  z-index: 10;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (--not-narrow-vp) {
  .main-nav__list {
    position: static;
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    margin-left: 20px;
  }
}
@media (--narrow-vp) {
  .main-nav__list--closed {
    display: none;
  }
}

@media (--narrow-vp) {
  .main-nav__item {
    --nav-item-bg-color: var(--bg-color-main);
    background-color: var(--nav-item-bg-color);
    box-shadow: 30px 0 0 0 var(--nav-item-bg-color), -30px 0 0 0 var(--nav-item-bg-color);
  }
}
@media (--not-narrow-vp) {
  .main-nav__item {
    background: none;
  }
  .main-nav__item:not(:first-child) {
    margin-left: 35px;
  }
}

.main-nav__link {
  position: relative;
  display: block;
  width: 100%;
  padding: 22px 20px;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: var(--text-color-accent);
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
}
@media (--not-narrow-vp) {
  .main-nav__link {
    position: relative;
    padding: 0;
    border: none;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
  .main-nav__link[aria-current=page]::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -10px;
    left: 0;
    display: block;
    height: 2px;
    background-color: var(--bg-color-accent);
  }
}
@media (--narrow-vp) {
  .main-nav__link::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    display: block;
    height: 1px;
    background-color: var(--border-color-light);
    box-shadow: 30px 0 0 0 var(--border-color-light), -30px 0 0 0 var(--border-color-light);
  }
}

@media (--wide-vp) {
  .main-nav--index .main-nav__link {
    color: var(--text-color-contrast);
  }
  .main-nav--index .main-nav__link[aria-current=page]::after {
    background-color: var(--bg-color-main);
  }
}

.logo {
  flex-grow: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 12px 0 16px;
  text-decoration: none;
}
.logo[aria-current=page] {
  pointer-events: none;
}
@media (--not-narrow-vp) {
  .logo {
    flex-grow: 0;
    flex-shrink: 0;
    width: 174px;
    height: 52px;
    padding: 0;
  }
}
@media (--wide-vp) {
  .logo {
    width: 202px;
    height: 60px;
  }
}
.logo--footer {
  display: block;
  padding: 39px 20px 32px;
  text-align: center;
}
@media (--not-narrow-vp) {
  .logo--footer {
    padding: 0;
    text-align: left;
  }
}

.logo__image {
  margin-right: 56px;
}
@media (--not-narrow-vp) {
  .logo__image {
    margin-right: 12px;
  }
}

.main-footer {
  background-color: var(--bg-color-darken);
}

.main-footer__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (--not-narrow-vp) {
  .main-footer__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 20px;
    padding: 53px 0;
  }
}

@media (--narrow-vp) {
  .main-footer__item {
    width: 100%;
  }
  .main-footer__item:not(:last-child) {
    border-bottom: 1px solid var(--border-color-dark);
  }
}

.social {
  padding: 20px;
}
@media (--not-narrow-vp) {
  .social {
    padding: 0;
  }
}

.social__list {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.social__item {
  margin: 15px;
}

.social__link {
  display: block;
  width: 22px;
  height: 22px;
  background-color: var(--bg-color-accent);
}

.social__icon {
  fill: currentColor;
}

.creators {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 22px 0;
  color: var(--text-color-main);
  text-decoration: none;
}
@media (--not-narrow-vp) {
  .creators {
    justify-content: flex-end;
    padding: 0;
  }
}
.creators::after {
  content: "";
  display: block;
  width: 27px;
  height: 34px;
  margin-left: 22px;
  background-color: var(--bg-color-accent);
}

.banner {
  position: relative;
}
@media (--wide-vp) {
  .banner {
    position: static;
  }
}
.banner::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 0;
  display: block;
  width: 100%;
  height: 302px;
  background-color: var(--bg-color-accent);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (--middle-vp) {
  .banner::before {
    display: none;
  }
}
@media (--wide-vp) {
  .banner::before {
    top: 0;
    left: 50%;
    z-index: -1;
    width: 50%;
    height: 696px;
  }
}

.banner__container {
  position: relative;
  z-index: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (--not-narrow-vp) {
  .banner__container {
    align-items: flex-start;
    background-image: none;
  }
}

.banner__content {
  padding-top: 27px;
  font-family: var(--accent-font);
  font-weight: 400;
  text-align: center;
  color: var(--text-color-contrast);
}
@media (--not-narrow-vp) {
  .banner__content {
    width: 440px;
    text-align: left;
    color: var(--text-color-accent);
  }
}
@media (--wide-vp) {
  .banner__content {
    margin-left: 80px;
    padding-top: 82px;
  }
}

.banner__title {
  margin-top: 0;
  margin-bottom: 25px;
  font-weight: inherit;
  font-size: 36px;
  line-height: 36px;
  color: inherit;
}
@media (--not-narrow-vp) {
  .banner__title {
    margin-bottom: 41px;
    font-size: 60px;
    line-height: 60px;
  }
}

.banner__text {
  margin-top: 0;
  margin-bottom: 29px;
  font-weight: inherit;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
}
@media (--not-narrow-vp) {
  .banner__text {
    margin-bottom: 49px;
    font-size: 20px;
    line-height: 20px;
  }
}
@media (--wide-vp) {
  .banner__text {
    margin-bottom: 52px;
  }
}

.banner__image {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (--narrow-vp) {
  .banner__image {
    margin-bottom: 3px;
  }
}
@media (--middle-vp) {
  .banner__image {
    z-index: -1;
    order: 1;
    align-self: center;
    margin-top: -83px;
  }
}
@media (--wide-vp) {
  .banner__image {
    position: absolute;
    top: 0;
    right: 220px;
  }
}

.banner__link {
  width: 100%;
}
@media (--not-narrow-vp) {
  .banner__link {
    width: 244px;
  }
}
@media (--wide-vp) {
  .banner__link {
    margin-left: 80px;
    margin-bottom: 185px;
  }
}

.modules {
  background-color: var(--bg-color-main);
}

.modules__container {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 20px;
}
@media (--not-narrow-vp) {
  .modules__container {
    row-gap: 30px;
  }
}
@media (--wide-vp) {
  .modules__container {
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
  }
}

.module-card {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 20px;
  padding-bottom: 0;
  background-color: var(--bg-color-darken);
}
@media (--not-narrow-vp) {
  .module-card {
    min-height: 372px;
    padding: 53px 316px 53px 77px;
  }
}
@media (--wide-vp) {
  .module-card {
    padding: 47px 50px 55px 52px;
  }
}

@media (--narrow-vp) {
  .module-card__content {
    border-bottom: 1px solid var(--border-color-dark);
  }
}

.module-card__title {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 0 0 22px;
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  color: var(--text-color-accent);
}
@media (--middle-vp) {
  .module-card__title {
    margin-bottom: 24px;
    font-size: 36px;
    line-height: 36px;
  }
}
@media (--wide-vp) {
  .module-card__title {
    margin-bottom: 40px;
  }
}
.module-card__title::before {
  --module-card-icon-size: 50px;
  content: "";
  flex-shrink: 0;
  display: block;
  width: var(--module-card-icon-size);
  height: var(--module-card-icon-size);
  margin-right: 18px;
  border-radius: 50%;
  background-color: var(--bg-color-accent);
}
@media (--middle-vp) {
  .module-card__title::before {
    --module-card-icon-size: 200px;
    position: absolute;
    top: 90px;
    right: 62px;
    margin-right: 0;
  }
}
@media (--wide-vp) {
  .module-card__title::before {
    --module-card-icon-size: 100px;
    margin-right: 62px;
  }
}

.module-card__text {
  margin-top: 0;
  font-family: var(--main-font);
  color: var(--text-color-main);
}
@media (--not-narrow-vp) {
  .module-card__text {
    margin-bottom: 48px;
  }
}
@media (--wide-vp) {
  .module-card__text {
    margin-bottom: 23px;
  }
}

.module-card__link {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 15px 0 18px;
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  text-decoration: none;
  color: var(--text-color-accent);
}
.module-card__link:hover {
  opacity: 0.3;
}
.module-card__link::after {
  content: "";
  display: block;
  width: 32px;
  height: 10px;
  margin-left: 15px;
  background-color: var(--bg-color-accent);
}
@media (--not-narrow-vp) {
  .module-card__link {
    margin-top: auto;
    border-top: none;
    font-size: 20px;
    line-height: 30px;
  }
}

.features {
  padding-bottom: 49px;
}
@media (--middle-vp) {
  .features {
    border-bottom: 1px solid var(--border-color-light);
  }
}
@media (--not-narrow-vp) {
  .features {
    padding-bottom: 47px;
  }
}

.features__title {
  margin-top: 0;
  margin-bottom: 40px;
}
@media (--not-narrow-vp) {
  .features__title {
    margin-bottom: 72px;
  }
}

.features__list {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 20px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  counter-reset: features;
}
@media (--not-narrow-vp) {
  .features__list {
    grid-template-columns: 1fr 1fr;
    column-gap: 90px;
    row-gap: 90px;
  }
}
@media (--wide-vp) {
  .features__list {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 80px;
  }
}

.features__item {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  counter-increment: features;
}
@media (--not-narrow-vp) {
  .features__item {
    flex-direction: column;
    align-items: flex-start;
    min-height: 227px;
  }
}
.features__item::before {
  --feature-icon-size: 60px;
  content: "";
  flex-shrink: 0;
  display: block;
  width: var(--feature-icon-size);
  height: var(--feature-icon-size);
  margin-right: 20px;
  background-color: var(--bg-color-accent);
}
@media (--not-narrow-vp) {
  .features__item::before {
    --feature-icon-size: 80px;
    margin-bottom: 31px;
  }
}
@media (--not-narrow-vp) {
  .features__item::after {
    content: counter(features);
    position: absolute;
    top: 0;
    left: 120px;
    z-index: -1;
    display: block;
    font-family: var(--accent-font);
    font-size: 280px;
    line-height: 200px;
    font-weight: 400;
    color: var(--text-color-darken);
  }
}

