    bottom: 0;
    left: 0;
    border: 1px solid var(--border-color-light);
  }
}
.catalog-all::after {
  bottom: 0;
}

.catalog-all__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
}

.catalog-all__title {
  margin-top: 0;
  margin-bottom: 8px;
  padding-top: 53px;
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  color: #111111;
}
@media (--not-narrow-vp) {
  .catalog-all__title {
    max-width: 150px;
    margin-bottom: 23px;
    padding-top: 161px;
    font-size: 20px;
    line-height: 22px;
  }
}

.catalog-all__text {
  margin-top: 0;
  margin-bottom: 21px;
}
@media (--not-narrow-vp) {
  .catalog-all__text {
    max-width: 170px;
    margin-bottom: 0;
    line-height: 21px;
  }
}

.catalog-all__button {
  margin-top: auto;
}

.additional__container {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 35px;
}
@media (--not-narrow-vp) {
  .additional__container {
    row-gap: 69px;
  }
}
@media (--wide-vp) {
  .additional__container {
    grid-template-columns: 1fr 245px;
    column-gap: 79px;
    row-gap: 80px;
  }
}

.additional__title {
  display: flex;
  align-items: center;
  margin: 0;
  font-size: 24px;
  line-height: 24px;
}
@media (--not-narrow-vp) {
  .additional__title {
    margin-bottom: 11px;
  }
}
@media (--wide-vp) {
  .additional__title {
    grid-column: 1/-1;
    margin-bottom: 0;
  }
}
.additional__title::after {
  --title-decoration-color: var(--bg-color-accent);
  content: "";
  flex-grow: 1;
  display: block;
  height: 2px;
  margin-left: 22px;
  background-color: var(--title-decoration-color);
  box-shadow: 30px 0 0 0 var(--title-decoration-color);
}

.additional__list {
  width: 100%;
  border: none;
  border-collapse: collapse;
}

.additional-item {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 13px 0 19px;
  border-top: 1px solid var(--border-color-darken);
}
@media (--not-narrow-vp) {
  .additional-item {
    grid-template-columns: 50% 1fr 93px;
    padding: 26px 38px 28px;
  }
}
@media (--wide-vp) {
  .additional-item {
    grid-template-columns: 1fr 245px 80px 245px;
    align-items: center;
    padding: 15px 0 17px;
  }
}
.additional-item:last-child {
  border-bottom: 1px solid var(--border-color-darken);
}

.additional-item__title {
  grid-column: 1/-1;
  width: 100%;
  margin-bottom: 13px;
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: var(--text-color-dark);
  text-transform: uppercase;
  text-align: left;
}
@media (--not-narrow-vp) {
  .additional-item__title {
    grid-column: auto;
    font-size: 20px;
    line-height: 20px;
  }
}
@media (--wide-vp) {
  .additional-item__title {
    margin: 0;
  }
}

.additional-item__count {
  margin-bottom: 15px;
}
@media (--not-narrow-vp) {
  .additional-item__count {
    text-align: right;
  }
}
@media (--wide-vp) {
  .additional-item__count {
    margin: 0;
    text-align: left;
  }
}

.additional-item__price {
  margin-bottom: 15px;
  text-align: right;
}
@media (--wide-vp) {
  .additional-item__price {
    margin: 0;
    text-align: left;
  }
}

.additional-item__order {
  grid-column: 1/-1;
  width: 100%;
}
@media (--wide-vp) {
  .additional-item__order {
    grid-column: auto;
  }
}

.additional-item__button {
  width: 100%;
}

.additional-all {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  min-height: 290px;
  padding: 59px 60px 43px;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: var(--text-color-contrast);
  text-decoration: none;
  background-color: var(--bg-color-accent);
  background-image: url("../img/bg-order-mobile.jpg");
  background-size: auto;
  background-position: center top;
  background-repeat: no-repeat;
}
@media (--not-narrow-vp) {
  .additional-all {
    flex-direction: row;
    min-height: 200px;
    padding: 57px 124px 60px 77px;
  }
}
@media (--wide-vp) {
  .additional-all {
    flex-direction: column;
    padding: 55px 40px 40px;
  }
}
.additional-all::before {
  content: "";
  display: block;
  width: 80px;
  height: 80px;
  background-color: var(--bg-color-main);
}

.additional-all__text {
  max-width: 165px;
  text-align: center;
}
@media (--narrow-vp) {
  .additional-all__text {
    text-align: left;
  }
}